


















import {
  Component, Model, Prop, Vue, Emit,
} from 'vue-property-decorator';
import { IQuestionItem } from '@/types/interface';

@Component
export default class QuestionItem extends Vue {
  @Prop() quz!: IQuestionItem;
  @Model('change', { default: () => [] }) readonly selected!: string[];

  changeHandler(e: any, ans: string) {
    if (this.quz.qType === '1') {
      if (e.target.checked) this.$emit('change', [ans]);
      else this.$emit('change', []);
    } else {
      const temp = this.selected.map((d: string) => d);
      const idx = temp.indexOf(ans);
      if (idx < 0) temp.push(ans);
      else temp.splice(idx, 1);
      this.$emit('change', temp);
    }
  }

  @Emit()
  checkHandler(e: any) {
    return {
      checked: e.target.checked,
      num: this.quz.qNum,
    };
  }
}
