



































import { Component, Vue } from 'vue-property-decorator';
import Swal from 'sweetalert2';
import { IQuestionItem } from '@/types/interface';
import banner from '../../components/Banner.vue';
import questionItem from '../../components/QuestionItem.vue';
import questionInfoPanel from '../../components/QuestionInfoPanel.vue';

interface CheckItem {
  checked: boolean;
  num: number;
}

@Component({
  components: {
    banner,
    questionItem,
    questionInfoPanel,
  },
})

export default class MainPage extends Vue {
  timerSec = 0;
  intervalFunc = 0;
  select = [[], [], [], []];
  needCheck = [false, false, false, false];

  testExample = {
    type: '平時測驗 / 台南文史',
    year: '近三年考題',
    subject: '台南文史',
    amount: '22',
  }

  quzList = [
    {
      qNum: 1,
      question: '題目XXXXXX',
      qType: '1',
      answer: [
        'A',
        'B',
        'C',
        'D',
      ],
      right: ['B'],
    },
    {
      qNum: 2,
      question: '題目XXXXXX',
      qType: '2',
      answer: [
        'A',
        'B',
        'C',
        'D',
      ],
      right: ['B', 'C'],
    },
    {
      qNum: 3,
      question: '題目XXXXXX',
      qType: '1',
      answer: [
        'A',
        'B',
        'C',
        'D',
      ],
      right: ['A'],
    },
    {
      qNum: 4,
      question: '題目XXXXXX',
      qType: '2',
      answer: [
        'A',
        'B',
        'C',
        'D',
      ],
      right: ['B', 'C', 'A'],
    },
  ];

  get timer() {
    const hour = Math.floor(this.timerSec / 3600);
    const min = Math.floor((this.timerSec - (hour * 3600)) / 60);
    const sec = (this.timerSec - (hour * 3600) - (min * 60));
    return `${hour < 10 ? '0' : ''}${hour}:${min < 10 ? '0' : ''}${min}:${sec < 10 ? '0' : ''}${sec}`;
  }

  get answerList() {
    const temp = [];
    for (let i = 0; i < this.quzList.length; i += 1) {
      if (this.select[i] && this.select[i].length !== 0) temp.push(this.quzList[i].qNum);
    }
    return temp;
  }

  get noAnswerList() {
    const temp = [];
    for (let i = 0; i < this.quzList.length; i += 1) {
      if (this.answerList.indexOf(this.quzList[i].qNum) < 0) temp.push(this.quzList[i].qNum);
    }
    return temp;
  }

  get checkList() {
    const temp = [];
    for (let i = 0; i < this.quzList.length; i += 1) {
      if (this.needCheck[i]) temp.push(this.quzList[i].qNum);
    }
    return temp;
  }

  created() {
    this.intervalFunc = setInterval(() => {
      this.timerSec += 1;
    }, 1000);
  }

  check(res: CheckItem) {
    const idx = this.quzList.map((d: IQuestionItem) => d.qNum).indexOf(res.num);
    Vue.set(this.needCheck, idx, res.checked);
  }

  leave() {
    Swal.fire({
      width: 288,
      showCloseButton: false,
      showCancelButton: true,
      background: '#FFFFFF',
      title: '確定離開作答',
      text: '後續可至我的測驗紀錄繼續作答',
      customClass: {
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button-half',
        cancelButton: 'modal-cancel-button-half',
      },
      confirmButtonText: '確定',
      cancelButtonText: '取消',
      reverseButtons: true,
    }).then((res) => {
      if (res.isConfirmed) this.$router.push('/main');
    });
  }

  submitCheck() {
    Swal.fire({
      width: 312,
      showCloseButton: false,
      background: '#FFFFFF',
      title: '交卷前再檢查',
      html: '<table class="font-16-normal">'
        + `<tr><td style="color: #E10101; width: 55px;">未答</td>
            <td style="width: 210px; text-align: left; padding-left: 9px">
            ${this.noAnswerList.length === 0 ? '無' : this.noAnswerList.join(',')}</td></tr>`
        + `<tr><td style="width: 55px;">已答</td>
            <td style="width: 210px; text-align: left; padding-left: 9px">
            ${this.answerList.length === 0 ? '無' : this.answerList.join(',')}</td></tr>`
        + `<tr><td style="width: 55px;">檢查</td>
            <td style="width: 210px; text-align: left; padding-left: 9px">
            ${this.checkList.length === 0 ? '無' : this.checkList.join(',')}</td></tr>`
        + '</table>',
      customClass: {
        popup: 'modal-popup',
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button',
      },
      confirmButtonText: '確定',
    });
  }

  submit() {
    if (this.noAnswerList.length !== 0) return;
    Swal.fire({
      width: 312,
      showCloseButton: false,
      showCancelButton: true,
      background: '#FFFFFF',
      title: '交卷前再檢查',
      html: '<table class="font-16-normal">'
        + `<tr><td style="color: #E10101; width: 65px;">未答</td>
            <td style="width: 215px; text-align: left; padding-left: 9px">
            ${this.noAnswerList.length === 0 ? '無' : this.noAnswerList.join(',')}</td></tr>`
        + `<tr><td style="width: 65px;">已答</td>
            <td style="width: 215px; text-align: left; padding-left: 9px">
            ${this.answerList.length === 0 ? '無' : this.answerList.join(',')}</td></tr>`
        + `<tr><td style="width: 65px;">檢查</td>
            <td style="width: 215px; text-align: left; padding-left: 9px">
            ${this.checkList.length === 0 ? '無' : this.checkList.join(',')}</td></tr>`
        + '</table>',
      customClass: {
        container: 'modal-container',
        popup: 'modal-popup',
        title: 'modal-title',
        content: 'modal-content',
        confirmButton: 'modal-confirm-button-130',
        cancelButton: 'modal-cancel-button-130',
      },
      confirmButtonText: '送出答案',
      cancelButtonText: '繼續作答',
      reverseButtons: true,
    }).then((res) => {
      if (res.isConfirmed) {
        Swal.fire({
          width: 288,
          showCloseButton: false,
          background: '#FFFFFF',
          title: '確認送出',
          text: '您的回答已送出，確認後觀看解答',
          customClass: {
            title: 'modal-title',
            content: 'modal-content',
            confirmButton: 'modal-confirm-button',
          },
          confirmButtonText: '確定',
        }).then((result) => {
          if (result.isConfirmed) this.$router.push('/test/solution_page');
        });
      }
    });
  }

  destroy() {
    clearInterval(this.intervalFunc);
  }
}
